.footer {
	padding: 18px 24px 12px 20px;
	font-size: 13px;
	line-height: 16px;
	color: $color-night-rider;

	&__top {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
	}

	&__copyright {
		font-size: 13px;
		line-height: 16px;
	}

	&__privacy {
		position: relative;
		top: -2px;
		font-size: 13px;
		line-height: 16px;

		a {
			outline: 0;
			border: 0;
			border-bottom: 1px solid $color-night-rider;
			text-decoration: none;
			color: $color-night-rider;
		}
	}

	&__develop {
		display: flex;
		margin-top: 8px;
	}

	&__text-develop {
		margin-right: 7px;
	}
}