.button {
  display: block;
  outline: 0;
  border: 0;
  padding: 0;

  &--form {
    width: 100%;
    height: 52px;
    font-family: $font-museo;
    font-weight: 700;
    font-size: 16px;
    text-align: center;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: $color-white;
    background: $color-burnt-sienna;
  }

  &--red {
    width: 100%;
    height: 52px;
    font-family: $font-museo;
    font-weight: 700;
    font-size: 16px;
    line-height: 52px;
    text-align: center;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: $color-white;
    background: $color-burnt-sienna;
  }
}