.mobile-panel {
	position: fixed;
	left: 0;
	top: 0;
	z-index: 10;
	overflow: auto;
	padding: 0 20px;
	width: 100%;
	height: 100%;
	color: $color-white;
	background: $color-night-rider;

	&__header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 17px 0;
	}

	&__back {
		width: 23px;
		height: 17px;
	}

	&__phone {
		display: flex;
		align-items: center;
		color: $color-white;
	}

	&__icon-phone {
		margin-right: 11px;
		width: 27px;
		height: 27px;
	}

	&__number-phone {
		font-weight: 700;
		font-size: 16px;
		line-height: 19px;
	}

	&__nav {
		list-style: none;
		margin: 0;
		margin-top: 18px;
		padding: 0;

		li {
			margin-bottom: 21px;

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	&__item-nav {
		font-weight: 700;
		font-size: 16px;
		line-height: 19px;
		color: $color-white;
	}

	&__lang {
		display: flex;
		margin-top: 41px;
	}

	&__item-lang {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 40px;
		height: 40px;
		font-weight: 700;
		font-size: 16px;
		text-transform: uppercase;
		color: $color-white;
		background: $color-nero;

		&.is-current {
			background: $color-burnt-sienna;
		}
	}
}