*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
}

body {
	font-family: $font-museo;

	&.is-menu-open {
		position: fixed;
		overflow: hidden;
	}
}

a {
	text-decoration: none;

	&:focus,
	&:active {
		outline: 0;
	}
}

.container {
	@include desktop {
		margin: auto;
		padding: 0 10px;
		width: 1200px;
	}
}

.svg-icon {
	svg {
		display: block;
		width: 100%;
		height: 100%;
	}
}

button {
	outline: 0;
	border: 0;
	padding: 0;
	background: transparent;
}

h2 {
	margin: 0;
	font-family: $font-museo;
	font-weight: 900;
	font-size: 28px;
	line-height: 34px;
	color: $color-night-rider;
}

h3 {
	margin: 0;
	margin-bottom: 15px;
	font-family: $font-museo;
	font-weight: 900;
	font-size: 24px;
	line-height: 29px;
	color: $color-night-rider;
}

.text-center {
	text-align: center;
}

.for-desktop {
	@media (max-width: 1199px) {
		display: none !important;
	}
}

.is-hidden {
	display: none !important;
}