.ui-helper-hidden {
	display: none;
}
.ui-helper-hidden-accessible {
	border: 0;
	clip: rect(0 0 0 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}
.ui-helper-reset {
	margin: 0;
	padding: 0;
	border: 0;
	outline: 0;
	line-height: 1.3;
	text-decoration: none;
	font-size: 100%;
	list-style: none;
}
.ui-helper-clearfix:before,
.ui-helper-clearfix:after {
	content: "";
	display: table;
	border-collapse: collapse;
}
.ui-helper-clearfix:after {
	clear: both;
}
.ui-helper-zfix {
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	position: absolute;
	opacity: 0;
	filter:Alpha(Opacity=0); /* support: IE8 */
}

.ui-front {
	z-index: 100;
}

.ui-state-disabled {
	cursor: default !important;
	pointer-events: none;
}

.ui-icon {
	display: inline-block;
	vertical-align: middle;
	margin-top: -.25em;
	position: relative;
	text-indent: -99999px;
	overflow: hidden;
	background-repeat: no-repeat;
}

.ui-widget-icon-block {
	left: 50%;
	margin-left: -8px;
	display: block;
}

/* Component containers
----------------------------------*/
.ui-widget {
  font-family: $font-museo;
  font-size: 14px;
}

.ui-widget .ui-widget {
	font-size: 1em;
}
.ui-widget input,
.ui-widget select,
.ui-widget textarea,
.ui-widget button {
  font-family: $font-museo;
}

.ui-widget-content {
	background: #ffffff;
}

.ui-widget-header a {
  position: absolute;
  display: block;
  padding: 5px;

  span {
    display: block;
    width: 7px;
    height: 11px;
  }

  &.ui-datepicker-prev {
    left: 15px;
    top: 27px;

    span {
      background: url("../images/icons/calendar-left.svg");
    }
  }

  &.ui-datepicker-next {
    right: 15px;
    top: 27px;

    span {
      background: url("../images/icons/calendar-right.svg");
    }
  }
}

.ui-datepicker {
  z-index: 11 !important;
  display: none;
  margin-top: 2px;
  padding-bottom: 14px;
  width: calc(100% - 40px);
  color: $color-night-rider;
}

.ui-datepicker-title {
  padding: 22px 15px;
  padding-bottom: 16px;
  font-weight: 900;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
}

.ui-datepicker-calendar {
  width: 100%;

  thead {
    
    font-size: 12px;

    th {
      font-family: $font-museo;
      font-weight: 900;
      padding: 0 5px;
      padding-bottom: 7px;
    }
  }

  tbody {
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
  }

  td {
    text-align: center;

    a {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      margin: auto;
      width: 34px;
      height: 34px;
      color: $color-night-rider;

      &.ui-state-active {
        background: $color-gorse;
      }
    }
  }
}