.home {
  &__top {
    @include desktop {
      height: 609px;
      background: url("../images/main/car-main-top.jpg") no-repeat 50%;
      background-size: cover;
    }
  }

  &__container-top {
    @include desktop {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      height: 100%;
    }
  }

  &__img-top {
    position: relative;
    height: vw(215, 375);
    background: url("../images/main/car-main-top.jpg") no-repeat 20% 8%;
    background-size: vw(528, 375);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      z-index: 2;
      display: block;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, $color-black -31.19%, rgba(0, 0, 0, 0) 78.32%);
      transform: matrix(-1, 0, 0, 1, 0, 0);
    }

    @include laptop {
      height: vw(332, 768);
      background-position: 48% -102%;
      background-size: vw(770, 768);
    }
  }

  &__form-top {
    padding: 20px;
    background: $color-gorse;

    @include desktop {
      padding: 43px 45px;
      padding-right: 47px;
      padding-left: 46px;
      width: 428px;
      height: 467px;
      background: rgba($color-gorse, 0.9);
    }
  }

  &__title-form {
    margin-bottom: 11px;
    text-align: center;

    @include desktop {
      margin-bottom: 20px;
    }
  }

  &__models {
    padding: 19px;
    background: url("../images/main/models-mobile.jpg") no-repeat 50% 100%;
    background-size: cover;
  }

  &__title-models {
    text-align: center;
  }

  &__text-models {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: vw(299, 375);
  }

  &__offer {
    padding: 25px 20px;
    background: #f2f2f2;
  }

  &__title-offer {
    margin-bottom: 11px;
  }

  &__item-offer {
    position: relative;
    z-index: 3;
    overflow: hidden;
    margin-bottom: 10px;
    height: vw(184, 375);

    &:last-child {
      margin-bottom: 0;
    }

    .slider-block & {
      margin-bottom: 0;
    }
  }

  &__img-offer {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: 50%;
  }

  &__descr-offer {
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 2;
    padding: 17px 30px;
    width: 100%;
    font-weight: 900;
    font-size: 19px;
    line-height: 23px;
    color: $color-white;

    &::before {
      content: "";
      position: absolute;
      left: 30px;
      top: 6px;
      display: block;
      width: 108px;
      height: 3px;
      background: $color-gorse;
    }
  }

  &__news {
    margin-top: -132px;
    padding: 122px 20px 20px 20px;
    background: $color-gorse;
  }

  &__title-news {
    margin-bottom: 14px;
    font-size: 24px;
  }

  &__img-news {
    img {
      max-width: 100%;
      height: auto;
    }
  }

  &__date-news {
    margin-top: 12px;
    font-size: 12px;
    line-height: 14px;
    color: $color-night-rider;
  }

  &__name-news {
    margin-top: 4px;
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    color: $color-night-rider;
  }

  &__about {
    padding-top: 36px;
    padding-bottom: 20px;
  }

  &__container-about {
    padding: 0 22px;
    font-weight: 300;
    font-size: 14px;
    line-height: 24px;
    color: $color-night-rider;

    p {
      margin: 0;
    }
  }

  &__title-about {
    margin-bottom: 12px;
  }

  &__advantages {
    padding-top: 3px;
  }

  &__title-advantages {
    margin-bottom: 15px;
  }

  &__container-advantages {
    padding: 0 20px;
  }

  &__item-advantages {
    display: flex;
    align-items: center;
    border-bottom: 1px solid $color-white;
    padding: 13px 14px;
    background: $color-white-smoke;
  }

  &__img-advantages {
    border-radius: 50%;
    width: 60px;
    height: 60px;

    img {
      border-radius: 50%;
      width: 100%;
      height: 100%;
    }
  }

  &__text-advantages {
    margin-left: 14px;
    width: calc(100% - 80px);
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    color: $color-night-rider;
  }

  &__review {
    padding-top: 35px;
  }

  &__title-review {
    margin-bottom: 15px;
  }

  &__slider-review {
    .slick-dots {
      margin-top: 10px;
    }
  }

  &__item-review {
    text-align: center;
    background: $color-white-smoke;

    img {
      display: inline-block;
      vertical-align: middle;
      max-width: 100%;
      height: auto;
    }
  }

  &__contacts {
    padding-top: 23px;
  }

  &__title-contacts {
    margin-bottom: 17px;
  }

  &__map-contacts {
    position: relative;
    padding: 0 10px;
    height: 400px;
    background: url("../images/map-mobile.jpg") no-repeat 50%;
    background-size: cover;
  }

  &__popup-contacts {
    position: absolute;
    left: 0;
    bottom: 20px;
    z-index: 2;
    width: 100%;
  }

  &__info-contacts {
    padding: 22px 25px 10px 15px;
    background: rgba($color-gorse, 0.9);
  }
}