.form-group {
  position: relative;
  margin-bottom: 17px;

  &:last-child {
    margin-bottom: 0;
  }

  &--submit-home-top {
    margin-top: -2px;
  }
}

.form-label {
  margin-bottom: 8px;

  &__title {
    font-weight: 900;
    font-size: 12px;
    line-height: 14px;
    text-transform: uppercase;
    letter-spacing: 0.06em;
    color: $color-night-rider;
  }

  &__link {
    font-weight: 700;
    font-size: 10px;
    line-height: 12px;
    text-transform: uppercase;
    letter-spacing: 0.06em;
    color: $color-royal-blue;
  }

  &--flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
  }
}

.form-datepicker {
  position: relative;
}

.form-select {
  display: block;
  outline: 0;
  border: 0;
  width: 100%;
  height: 50px;
  font-family: $font-museo;
  font-size: 14px;
  letter-spacing: -0.15px;
  color: $color-night-rider;

  &--1-2 {
    width: 49%;
  }

  @include desktop {
    height: 52px;
  }
}

.select-group {
  position: relative;
  display: flex;
  justify-content: space-between;
}

.select-item {
  font-weight: 300;

  &__button {
    position: relative;
    display: flex;
    align-items: center;
    padding: 0 18px;
    width: 100%;
    height: 50px;
    font-size: 14px;
    letter-spacing: -0.15px;
    color: $color-night-rider;
    background: $color-white;

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      right: 18px;
      z-index: 2;
      display: block;
      width: 10px;
      height: 7px;
      background: url("../images/icons/arrow.svg") no-repeat;
      transform: translateY(-50%);
      transition: transform 0.3s;

      .is-open-select & {
        transform: rotate(180deg);
        transform-origin: 50% 25%;
      }
    }
  }

  &__button-dropdown {
    padding: 6px 0;
    font-weight: 300;

    &.is-yellow {
      border-color: #fad842;
      color: $color-white;
      background: #fad842;
    }

    &.is-red {
      border-color: #eb5757;
      color: $color-white;
      background: #eb5757;
    }
  }

  &__dropdown {
    position: absolute;
    left: 0;
    z-index: 5;
    box-shadow: 0px 4px 34px rgba(51, 51, 51, 0.16);
    margin-top: 5px;
    padding: 12px 18px;
    width: 100%;
    background: $color-white;

    &--time {
      display: flex;
      flex-wrap: wrap;
      padding: 13px 15px;
      padding-bottom: 16px;

      .select-item__button-dropdown {
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid $color-gainsboro;
        border-radius: 100px;
        margin: 6px;
        width: 49px;
        height: 25px;
        font-weight: 300;
        font-size: 12px;
      }
    }
  }
}

.datepicker {
  display: block;
  outline: 0;
  border: 0;
  padding: 0 18px;
  width: 100%;
  height: 50px;
  font-size: 14px;
  letter-spacing: -0.15px;
  color: $color-night-rider;
  background: $color-white;

  + label {
    position: absolute;
    right: 19px;
    top: 50%;
    z-index: 2;
    display: block;
    width: 20px;
    height: 20px;
    background: url("../images/icons/calendar.svg");
    transform: translateY(-50%);
  }
}