.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 10;
  padding: 14px 20px;
  width: 100%;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__burger {
    outline: 0;
    border: 0;
    display: block;
    padding: 0;
    width: 22px;
    height: 17px;
    background: transparent;
  }

  &__logo {
    position: relative;
    left: 2px;
    display: block;
    width: 171px;
    height: 33px;

    img {
      max-width: 100%;
      height: auto;
    }

    @include laptop {
      position: absolute;
      left: 61px;
      top: 50%;
      transform: translateY(-50%);
    }

    @include desktop {
      right: 13px;
      top: 3px;
    }
  }

  &__nav {
    position: relative;

    @include desktop {
      right: 39px;
      top: -1px;
    }
  }

  &__item-nav {
    text-decoration: none;
    color: $color-white;
    transition: color 0.3s;

    &:focus,
    &:active {
      outline: 0;
    }

    &:hover {
      color: $color-burnt-sienna;
    }

    @include desktop {
      margin: 0 12.5px;
      font-size: 14px;
      line-height: 17px;
      letter-spacing: 0.15px;
    }
  }

  &__phone {
    position: relative;
    top: 1px;
    display: flex;
    align-items: center;
    font-weight: 500;
    color: $color-white;

    @include desktop {
      right: 14px;
      font-size: 14px;
    }
  }

  &__icon-phone {
    width: 27px;
    height: 27px;

    @include desktop {
      margin-right: 10px;
    }
  }

  &__button-lang {
    display: block;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    color: $color-white;
    background: $color-nero;
    transition: background 0.3s;

    &.is-current,
    &:hover {
      background: $color-burnt-sienna;
    }

    @include desktop {
      width: 40px;
      height: 40px;
      font-size: 14px;
      line-height: 40px;
    }
  }

  @include laptop {
    padding: 16px 20px;
    background: $color-night-rider;
  }
}